header {
  position: relative;
  width: 100%;
  background: $white;
  height: 50px;
  line-height: 50px;
  box-shadow: 0 -3px 0 darken($white, 10%) inset;

  .nav-wrap {
    max-width: 960px;
    margin: 0 auto;

    .logo {
      padding-left: 1em;

      img {
        width: 50px;
        vertical-align: middle; } }

    #menu-icon {
      display: block;
      position: absolute;
      top: 0;
      right: 1em;
      color: $gif-red;
      cursor: pointer;
      opacity: 1;
      padding: 0 .4em;

      &:hover {
        opacity: 0.85;
        border: none; } }

    .navbar {
      background: $white;
      float: none;
      max-height: 0;
      overflow: hidden;
      transition: max-height .4s;

      .menu {
        margin: 0;
        padding: 0;
        list-style-type: none;

        li {
          list-style-type: none;
          display: block;

          a {
            display: block;
            color: $gif-red;
            padding: 0 .8em;
            transition: all .2s ease;
            border-bottom: 1px solid lighten($white, 12%);

            &:hover {
              background-color: lighten($white, 15%); } } } } }

    .expand {
      max-height: 20em; } } }

@media only screen and (min-width: 768px) {
  .wrap {
    padding-left: 1em;
    padding-right: 1em;

    .logo {
      margin-left: 0; }

    .logo, .navbar {
      display: inline-block; } } }
